'use client';

import { useState } from 'react';
import { TaskResponse } from '../../../backend/src/models/task';
import { apiService } from '../services/api';

interface TaskItemProps {
  task: TaskResponse;
  onTaskUpdated: () => void;
  onTaskDeleted: () => void;
}

export default function TaskItem({ task, onTaskUpdated, onTaskDeleted }: TaskItemProps) {
  const [isEditing, setIsEditing] = useState(false);
  const [title, setTitle] = useState(task.title);
  const [description, setDescription] = useState(task.description || '');
  const [loading, setLoading] = useState(false);

  const handleToggleComplete = async () => {
    setLoading(true);
    try {
      // In a real implementation, we would call the API
      // await apiService.patch(`/users/${task.user_id}/tasks/${task.id}/complete`);
      onTaskUpdated();
    } catch (err) {
      console.error('Failed to toggle task completion:', err);
    } finally {
      setLoading(false);
    }
  };

  const handleUpdate = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    try {
      // In a real implementation, we would call the API
      // await apiService.put(`/users/${task.user_id}/tasks/${task.id}`, { title, description });
      onTaskUpdated();
      setIsEditing(false);
    } catch (err) {
      console.error('Failed to update task:', err);
    } finally {
      setLoading(false);
    }
  };

  const handleDelete = async () => {
    if (window.confirm('Are you sure you want to delete this task?')) {
      setLoading(true);
      try {
        // In a real implementation, we would call the API
        // await apiService.delete(`/users/${task.user_id}/tasks/${task.id}`);
        onTaskDeleted();
      } catch (err) {
        console.error('Failed to delete task:', err);
      } finally {
        setLoading(false);
      }
    }
  };

  if (isEditing) {
    return (
      <div className="task-item editing">
        <form onSubmit={handleUpdate}>
          <input
            type="text"
            value={title}
            onChange={(e) => setTitle(e.target.value)}
            required
          />
          <textarea
            value={description}
            onChange={(e) => setDescription(e.target.value)}
          />
          <button type="submit" disabled={loading}>
            {loading ? 'Saving...' : 'Save'}
          </button>
          <button type="button" onClick={() => setIsEditing(false)}>
            Cancel
          </button>
        </form>
      </div>
    );
  }

  return (
    <div className={`task-item ${task.completed ? 'completed' : ''}`}>
      <div className="task-content">
        <h3>{task.title}</h3>
        {task.description && <p>{task.description}</p>}
        <div className="task-meta">
          <span>Created: {new Date(task.created_at).toLocaleDateString()}</span>
          <span>Updated: {new Date(task.updated_at).toLocaleDateString()}</span>
        </div>
      </div>
      <div className="task-actions">
        <button
          onClick={handleToggleComplete}
          disabled={loading}
          className={task.completed ? 'completed-btn' : 'pending-btn'}
        >
          {task.completed ? 'Completed' : 'Mark Complete'}
        </button>
        <button onClick={() => setIsEditing(true)} disabled={loading}>
          Edit
        </button>
        <button onClick={handleDelete} disabled={loading} className="delete-btn">
          Delete
        </button>
      </div>
    </div>
  );
}