'use client';

import { useState, useEffect } from 'react';
import { TaskResponse } from '../../../backend/src/models/task';
import { apiService } from '../services/api';
import LoadingSpinner from './LoadingSpinner';
import ErrorDisplay from './ErrorDisplay';
import TaskItem from './TaskItem';

interface TaskListProps {
  userId: number;
}

export default function TaskList({ userId }: TaskListProps) {
  const [tasks, setTasks] = useState<TaskResponse[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [refreshTrigger, setRefreshTrigger] = useState(0);

  useEffect(() => {
    fetchTasks();
  }, [userId, refreshTrigger]);

  const fetchTasks = async () => {
    try {
      setLoading(true);
      setError(null);
      // In a real implementation, we would fetch from the API
      // const data = await apiService.get<TaskResponse[]>(`/users/${userId}/tasks`);
      // For now, we'll use mock data
      const mockTasks: TaskResponse[] = [
        {
          id: 1,
          title: 'Sample Task',
          description: 'This is a sample task',
          completed: false,
          user_id: userId,
          created_at: new Date(),
          updated_at: new Date(),
        },
        {
          id: 2,
          title: 'Another Sample Task',
          description: 'This is another sample task to show responsiveness',
          completed: true,
          user_id: userId,
          created_at: new Date(),
          updated_at: new Date(),
        }
      ];
      setTasks(mockTasks);
    } catch (err) {
      setError('Failed to load tasks');
      console.error(err);
    } finally {
      setLoading(false);
    }
  };

  const handleTaskUpdated = () => {
    setRefreshTrigger(prev => prev + 1);
  };

  const handleTaskDeleted = () => {
    setRefreshTrigger(prev => prev + 1);
  };

  if (loading) return <LoadingSpinner text="Loading tasks..." />;
  if (error) return <ErrorDisplay message={error} onRetry={fetchTasks} />;

  if (tasks.length === 0) {
    return (
      <div className="bg-white p-6 rounded-lg shadow text-center">
        <h2 className="text-xl font-semibold mb-2">No tasks yet</h2>
        <p className="text-gray-600">Create your first task to get started!</p>
      </div>
    );
  }

  return (
    <div className="bg-white p-4 rounded-lg shadow">
      <h2 className="text-xl font-semibold mb-4">Your Tasks</h2>
      <div className="space-y-4">
        {tasks.map(task => (
          <TaskItem
            key={task.id}
            task={task}
            onTaskUpdated={handleTaskUpdated}
            onTaskDeleted={handleTaskDeleted}
          />
        ))}
      </div>
    </div>
  );
}